import {MyCourseList} from '@/api/learn-record'
import KsFilter from '@/components/ks-components/filter/filter.vue'
import UniLoadMore from '@/components/uni/uni-load-more/uni-load-more.vue'
export default {
	data(){
		return{
			loaded:false,
			tabIndex:0,
			sortLists:[
				{name:'全部学习进度',type:0},
				{name:'未开始学习',type:1},
				{name:'学习中',type:2},
				{name:'已完成学习',type:3}
			],
			sortList:[
				{name:'按最新加入排序',type:0},
				{name:'按最近学习排序',type:1}
			],
			courseList:[],
			getParameters:{
				from:'app',
				sort:'0',//排序条件0,1
				screen:'0',//筛选条件0,1，2，3
				pageindex:1,
				pagesize:10,
			},
			pageCount:0,
			moreStatus:'more',
			islogin:false,
			state:{}
		}
	},
	components:{
		KsFilter,
		UniLoadMore
	},
	onLoad() {
		this.$store.ready(state=>{
			this.state = state
		})
	},
	onPullDownRefresh() {
		this.loadPage()
	},
	onShow() {
		uni.showLoading({
		    title: '加载中'
		});
		this.$store.ready(state=>{
			
			if(state.isLogin == 1){
				this.islogin = true
				this.loadPage()
			}else{
				uni.hideLoading();
				this.islogin = false
				this.loaded = true
			}
		})
	},
	onReachBottom(e){
		this.getParameters.pageindex++
		this.moreList()
	},
	methods:{
		open(item){
			if(this.state.isshowprice){
				var url = encodeURIComponent(item.playurl)
				uni.navigateTo({
					url:'/pages/course/courseplay?photo='+item.courseImg+'&courseid='+item.courseId+'&periodid='+item.lastLearnPeriodId,
				});
			}
			
		},
		getOrderid(id){
			this.getParameters.sort = id
			this.loadPage()
		},
		getOrderids(id){
			this.getParameters.screen = id
			this.loadPage()
		},
		//滚动加载更多的方法
		moreList(){
			if(this.getParameters.pageindex > this.pageCount){
				this.moreStatus = 'noMore'
			}else{
				this.moreStatus = 'loading'
				this.loadPage('more')
			}
			
		},
		//初始数据的请求
		loadPage(status){
			if(status=='more'){
				MyCourseList(this.getParameters).then(res=>{
					this.moreStatus = 'more'
					if(res.result){
						this.courseList = this.courseList.concat(res.data)
					}
					this.loaded = true
				})
			}else{
				this.getParameters.pageindex = 1
				MyCourseList(this.getParameters).then(res=>{
					if(res.result){
						this.pageCount = res.pageCount
						this.courseList = res.data
						if(this.pageCount==1){
							this.moreStatus = 'noMore'
						}else{
							this.moreStatus = 'more'
						}
						if(this.pageCount==1){
							this.moreStatus = 'noMore'
						}else{
							this.moreStatus = 'more'
						}
					}else{
						this.courseList = []
						this.pageCount = 0
					}
					this.loaded = true
					uni.stopPullDownRefresh();
					uni.hideLoading();
				})
			}
		}
	}
}
